<template>
  <div class="bindPhone">
    <div class="record-bar">
      <van-nav-bar
        title="绑定手机号"
        left-arrow
        @click-left="linkHome"
      />
    </div>
    <div class="bindPhone-box">
        <img class="bindPhone-img" src="../../../assets/my/replacePhone.png"/>
    </div>
      <div class="phone">已绑定的手机号</div>
      <div class="phone1" >{{phone}}</div>
    <van-button class="upBankCard-but" @click="linkIdentity">更换手机号</van-button>
  </div>
</template>

<script>
  import '../../../assets/font/iconfont'
  export default {
    name: "bindPhone",
    data(){
      return{
        phone:0
      }
    },
    created(){
      this.phone = localStorage.getItem("phone").substr(0,3)+"*****"+localStorage.getItem("phone").substr(8);
    },
    methods:{
      // 返回上级
      linkHome () {
        this.$router.go(-1)
      },
      linkIdentity(){
        this.$router.push({
          name:'identity'
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  @import "../../../style/my";
</style>



